import { Component, OnInit } from '@angular/core';
import { LiffService } from './services/liff.service';

declare var liff: any;
const SERVICE_UUID = 'c4dd444d-6d46-47de-8b24-c3b70fbf8b31';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.scss']
})
export class AppComponent implements OnInit {
  title = 'LIFF Mock';
  messages: string;
  userProfile: any;

  constructor(private liffService: LiffService) {
    this.messages = '';
    this.initLineLiff();
  }

  ngOnInit() {
    this.initLineLiff();
  }

  async initLineLiff() {
    try {
      const data: any = await this.liffService.initLineLiff();
      this.userProfile = await liff.getProfile();
      alert(`こんにちは！ ${this.userProfile.displayName}`);
    } catch (err) {
      alert(err);
    }
  }

  async sendMessages() {
    try {
      const successMsgs = await liff.sendMessages([
        {
          type: 'text',
          text: this.messages
        }
      ]);
      liff.closeWindow();

    } catch (e) {
      alert(e);
    }
  }
}
